
import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import Button from '@mui/material/Button';
import { Menu, MenuItem, MenuButton, SubMenu } from '@szhsin/react-menu';


import PersonOutlineOutlinedIcon from '@mui/icons-material/PersonOutlineOutlined';
import InsertCommentOutlinedIcon from '@mui/icons-material/InsertCommentOutlined';
import InsertPhotoOutlinedIcon from '@mui/icons-material/InsertPhotoOutlined';
import GroupsOutlinedIcon from '@mui/icons-material/GroupsOutlined';
import StackedLineChartOutlinedIcon from '@mui/icons-material/StackedLineChartOutlined';
import ArticleOutlinedIcon from '@mui/icons-material/ArticleOutlined';
import ControlPointDuplicateOutlinedIcon from '@mui/icons-material/ControlPointDuplicateOutlined';
import FaceOutlinedIcon from '@mui/icons-material/FaceOutlined';
import { IconButton } from '@mui/material';
import Logotip from "../logo.png";

const Header = () =>{

    const toNavigate = useNavigate()
    const out = () =>{
        localStorage.clear()
        toNavigate('/auth')
    }

  return (
    <div className='header_content'>
        <div>
           <Button onClick={()=>toNavigate('/main')} className='but_log'><img className='logo' src={Logotip}/></Button>
        </div>
        <div className='header'>
            <Menu menuButton={<Button className='menu_but button css-but' variant="outlined" startIcon={<PersonOutlineOutlinedIcon />}> </Button>}>
                <MenuItem onClick={()=>toNavigate('/user/get_followers/'+null)}></MenuItem>
                <MenuItem onClick={()=>toNavigate('/user/get_subscriptions/'+null)}></MenuItem>
                <MenuItem onClick={()=>toNavigate('/user/get_info/'+null)}></MenuItem>
                <MenuItem onClick={()=>toNavigate('/user/search/'+null)}></MenuItem>
            </Menu>
            <Menu menuButton={<Button className='menu_but button css-but' variant="outlined" startIcon={<InsertCommentOutlinedIcon />}> </Button>}>
                <MenuItem onClick={()=>toNavigate('/comments/about_post/'+null)}> </MenuItem>
                <MenuItem onClick={()=>toNavigate('/comments/about_photo/'+null)}> </MenuItem>
                <MenuItem onClick={()=>toNavigate('/comments/about_video/'+null)}> </MenuItem>
            </Menu>
            <Menu menuButton={<Button className='menu_but button css-but' variant="outlined" startIcon={<GroupsOutlinedIcon />}> </Button>}>
                <MenuItem onClick={()=>toNavigate('/groups/get_subscriptions/'+null)}></MenuItem>
                <MenuItem onClick={()=>toNavigate('/groups/get_info/'+null)}></MenuItem>
                <MenuItem onClick={()=>toNavigate('/groups/search/'+null)}> </MenuItem>
                <MenuItem onClick={()=>toNavigate('/groups/search_event/'+null)}> </MenuItem>
                <MenuItem onClick={()=>toNavigate('/groups/get_catalogs/'+null)}></MenuItem>
                <MenuItem onClick={()=>toNavigate('/groups/get_categories/'+null)}></MenuItem>
            </Menu>
            <Menu menuButton={<Button className='menu_but button css-but' variant="outlined" startIcon={<InsertPhotoOutlinedIcon />}> </Button>}>
                <MenuItem onClick={()=>toNavigate('/media/get_info_photo/'+null)}>  </MenuItem>
                <MenuItem onClick={()=>toNavigate('/media/search_photo/'+null)}> </MenuItem>
                <MenuItem onClick={()=>toNavigate('/media/get_info_video/'+null)}>  </MenuItem>
                <MenuItem onClick={()=>toNavigate('/media/search_video/'+null)}> </MenuItem>
                <MenuItem onClick={()=>toNavigate('/media/get_info_albom_video/'+null)}>    </MenuItem>
            </Menu>
            <Menu menuButton={<Button className='menu_but button css-but' variant="outlined" startIcon={<ArticleOutlinedIcon />}> </Button>}>
                <MenuItem onClick={()=>toNavigate('/wall/get_info/'+null)}></MenuItem>
                <MenuItem onClick={()=>toNavigate('/wall/search/'+null)}></MenuItem>
                <MenuItem onClick={()=>toNavigate('/wall/get_repost/'+null)}></MenuItem>
            </Menu>
            <Menu menuButton={<Button className='menu_but button css-but' variant="outlined" startIcon={<StackedLineChartOutlinedIcon />}> </Button>}>
                <MenuItem onClick={()=>toNavigate('/statistic/get_application/'+null)}></MenuItem>
                <MenuItem onClick={()=>toNavigate('/statistic/get_group/'+null)}>C</MenuItem>
                <MenuItem onClick={()=>toNavigate('/statistic/get_link/'+null)}> </MenuItem>
            </Menu>
            <Menu menuButton={<Button className='menu_but button css-but' variant="outlined" startIcon={<ControlPointDuplicateOutlinedIcon />}> </Button>}>
                <MenuItem onClick={()=>toNavigate('/other/get_country/'+null)}></MenuItem>
                <MenuItem onClick={()=>toNavigate('/other/get_regions/'+null)}></MenuItem>
                <MenuItem onClick={()=>toNavigate('/other/get_city/'+null)}></MenuItem>
                <MenuItem onClick={()=>toNavigate('/other/get_universities/'+null)}></MenuItem>
                <MenuItem onClick={()=>toNavigate('/other/get_faculties/'+null)}>/</MenuItem>
                <MenuItem onClick={()=>toNavigate('/other/get_identifikator/'+null)}> </MenuItem>
                <MenuItem onClick={()=>toNavigate('/other/get_shold_link/'+null)}> </MenuItem>
            </Menu>
        </div>
        <div>
            <Menu menuButton={<IconButton className='menu_but  button' color="primary"><FaceOutlinedIcon  variant="outlined"/></IconButton>}>
                <MenuItem onClick={()=>toNavigate('/history')}></MenuItem>
                <MenuItem onClick={()=> out()}></MenuItem>
            </Menu>
        </div>
    </div>
  );
}

export default Header;
